import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';


export default function Movies() {

  const [trendingMovies, setTrendingMovies] = useState([]);
  const baseImgeUrl = 'https://image.tmdb.org/t/p/original/'

  async function getTrendingItems() {
    let { data } = await axios.get(`https://api.themoviedb.org/3/trending/movie/week?api_key=c636ed7787cc302d96bf88ccf334e0d8&page=1`)
    setTrendingMovies(data.results)
  }


  useEffect(() => {
    getTrendingItems()
  }, [])

  return (<>
    <div className='container'>
      {trendingMovies ? <div className="row">
        <div className='col-md-4 d-flex align-items-center'>
          <div className='w-100'>
            <div className='bordr w-50 my-3'></div>
            <h4>Trending <br />Movie <br />to Wathch Now </h4>
            <p className='secondColor'>most watched movies by days</p>
            <div className='bordr my-3'></div>
          </div>
        </div>
        {trendingMovies.map((movie, index) =>
          <div className='col-md-2 my-3' key={index}>
            <div className='itemHover'>
              <Link to={`/noxe/moviedetails/${movie.id}`}>
                <img className='w-100' src={baseImgeUrl + movie.poster_path} alt="" />
                <span className='vote_home'>{movie.vote_average.toFixed(1)}</span>
                <h6 style={{ fontWeight: '600' }}>{movie.title}</h6>
              </Link>
            </div>
          </div>
        )}
      </div> : <i className='fs-1 fas fa-spinner fa-spin'></i>}
      <nav aria-label="...">
        <ul className="pagination pagination-sm justify-content-center">
          <li className="page-item active"><Link to={`/noxe/movies/page1`} className="page-link">1</Link></li>
          <li className="page-item"><Link to={`/noxe/movies/page/2`} className="page-link">2</Link></li>
          <li className="page-item"><Link to={`/noxe/movies/page/3`} className="page-link">3</Link></li>
          <li className="page-item"><Link to={`/noxe/movies/page/4`} className="page-link">4</Link></li>
          <li className="page-item"><Link to={`/noxe/movies/page/5`} className="page-link">5</Link></li>
        </ul>
      </nav>
    </div>
  </>
  )
}

