import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'

export default function TvDetails() {

  const [tv, setTv] = useState({})
  const baseImgeUrl = 'https://image.tmdb.org/t/p/original/'
  let params = useParams()
  const [tvsRecom, setTvsRecom] = useState([])
  const [casting, setCasting] = useState([])
  const [seasons, setSeasons] = useState([])


  async function gitMovieDetails(id) {
    let { data } = await axios.get(`https://api.themoviedb.org/3/tv/${params.id}?api_key=c636ed7787cc302d96bf88ccf334e0d8`);
    let responseRecom = await axios.get(`https://api.themoviedb.org/3/tv/${params.id}/recommendations?api_key=c636ed7787cc302d96bf88ccf334e0d8`);
    let responseCast = await axios.get(`https://api.themoviedb.org/3/tv/${params.id}/credits?api_key=c636ed7787cc302d96bf88ccf334e0d8`);
    setTv(data)
    setTvsRecom(responseRecom.data.results)
    setCasting(responseCast.data.cast)
    setSeasons(data.seasons)
    console.log(seasons);

  }



  useEffect(() => {
    gitMovieDetails()
  }, [])


  return <>
    <div className='back container_details mt-5' style={{ backgroundImage: `url(${baseImgeUrl}${tv.backdrop_path})` }}>
      <div className="row">
        <div className="col-lg-4">
          <img className='w-75' src={baseImgeUrl + tv.poster_path} alt="" />
        </div>
        <div className="col-lg-8">
          <div className="row mt-5">
            <div className="col-lg-12">
              <h3 className='title_details'>{tv.name}</h3>
              <p>
                <span style={{ fontSize: 'larger' }}>tagline : </span>
                <span>{tv.tagline}</span>
              </p>
              <p>
                <span style={{ fontSize: 'larger' }}>status : </span>
                <span className='click'>{tv.status}</span>
              </p>
              <p>
                <span>vote average : </span>
                <span className='click'>{parseInt(tv.vote_average)}</span>
              </p>
              <p className='mt-4 '>{tv.overview}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div className='seasons mt-5 container_details'>
      <h4 style={{ fontWeight: '800' }} className='my-4'>Seasons</h4>
      <div className="row g-2 ">
        {seasons.map((season, index) =>
          <div className='col-lg-1' key={index}>
            <Link to={`/noxe/${tv.id}/${tv.name}/season/${season.season_number}`}>
              <img className='w-100' src={baseImgeUrl + season.poster_path} alt="" />
              <span>{season.name}</span>
            </Link>
          </div>
        )}
      </div>
    </div>
    <div className='cast_movie'>
      <div className="container_details mt-5">
        <h4 style={{ fontWeight: '800' }} className='my-4'>Casting</h4>
        <div className="row w-75 m-auto">
          {casting.map((cast, index) => index < 6 ?
            <div className='col-lg-2' key={index}>
              <div>
                <img className='w-100' src={baseImgeUrl + cast.profile_path} alt="" />
                <h6 style={{ fontWeight: '800' }}>{cast.name}</h6>
                <span>({cast.character})</span>
              </div>
            </div> : ''
          )}
        </div>
      </div>
    </div>
    <div>
      {tvsRecom.length !== 0 ?
        <div className='container_details mt-5'>
          <h4 style={{ fontWeight: '800' }} className='my-4'>Recommend movie</h4>
          <div className="row gy-3">
            {tvsRecom.map((tvRecom, index) => index < 12 ?
              <div className='col-lg-2 itemHover' key={index}>
                <Link to={`/noxe/tvdetails/${tvRecom.id}`}>
                  <img className='w-100' src={baseImgeUrl + tvRecom.poster_path} alt="" />
                  <span className='vote_details'>{tv.vote_average.toFixed(1)}</span>
                  <h6 style={{ fontWeight: '600' }}>{tvRecom.name}</h6>
                </Link>
              </div> : ''
            )}
          </div>
        </div> : 'No Recommend'}
    </div>
  </>
}
