import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';


export default function Tv() {

  const [trendingTv, setTrendingTv] = useState([]);
  const baseImgeUrl = 'https://image.tmdb.org/t/p/original/'

  async function getTrendingItems() {
    let { data } = await axios.get(`https://api.themoviedb.org/3/trending/tv/week?api_key=c636ed7787cc302d96bf88ccf334e0d8`)
    setTrendingTv(data.results)
  }

  useEffect(() => {
    getTrendingItems()
  }, [])

  return (<>
    <div className='container'>
      {trendingTv ? <div className="row">
        <div className='col-md-4 d-flex align-items-center'>
          <div className='w-100'>
            <div className='bordr w-50 my-3'></div>
            <h4>Trending <br />Tv <br />to Wathch Now </h4>
            <p className='secondColor'>most watched movies by days</p>
            <div className='bordr my-3'></div>
          </div>
        </div>
        {trendingTv.map((tv, index) =>
          <div className='col-md-2 my-3' key={index}>
            <div className='itemHover'>
              <Link to={`/noxe/tvdetails/${tv.id}`}>
                <img className='w-100' src={baseImgeUrl + tv.poster_path} alt="" />
                <span className='vote_home'>{tv.vote_average.toFixed(1)}</span>
                <h6 style={{ fontWeight: '600' }}>{tv.name}</h6>
              </Link>
            </div>
          </div>
        )}
      </div> : <i className='fs-1 fas fa-spinner fa-spin'></i>}
    </div>
  </>
  )
}
