import axios from 'axios';
import Joi from 'joi';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';

export default function Login(props) {

  let navigate = useNavigate();
  const [errorList, setErrorList] = useState('')
  const [isLoding, setIsLoding] = useState(false)
  const [error, setError] = useState('')
  const [user, setUser] = useState({
    email: '',
    password: '',
  });

  function getUser(e) {
    let myUser = { ...user };
    myUser[e.target.name] = e.target.value
    setUser(myUser)
  }

  async function submitLogin(e) {
    e.preventDefault();
    setIsLoding(true)
    let validationResult = validateRegidterForm(user);

    if (validationResult.error) {
      // list error
      setIsLoding(false)
      setErrorList(validationResult.error.details)
    }

    else {
      let { data } = await axios.post('https://route-egypt-api.herokuapp.com/signin', user);
      if (data.message === 'success') {
        // login
        localStorage.setItem('userToken', data.token)
        props.gitUserData()
        navigate('/noxe')
        setIsLoding(false)
      }
      else {
        // error
        setError(data.message)
        setIsLoding(false)
      }
    }

  }

  function validateRegidterForm() {
    let schema = Joi.object({
      email: Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net', 'yahoo'] } }),
      password: Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$')),
    });
    return schema.validate(user, { abortEarly: false })
  }

  return (
    <div className='container'>
      <h2 className='my-3'>Login Now</h2>
      <form className='py-4' onSubmit={submitLogin}>
        <label htmlFor="email">Email :</label>
        <input onChange={getUser} type="email" className='form-control my-3' name='email' id='email' />
        {errorList ? errorList.map((error) => error.context.key === 'email' ? <div className='alert-error'>The Email is incorrect</div> : '') : ''}
        {error ? <div className="alert-error">{error.substring(error.lastIndexOf(':') + 2)}</div> : ''}
        <label htmlFor="password">Password :</label>
        <input onChange={getUser} type="password" className='form-control my-3' name='password' id='password' />
        {errorList ? errorList.map((error) => error.context.key === 'password' ? <div className='alert-error'>The Password is incorrect</div> : '') : ''}
        <button className='btn btn-outline-info'>{isLoding ? <i className='fas fa-spinner fa-spin'></i> : "Login"}</button>
      </form>
    </div >
  )
}
