import './App.css';
import { Navigate, Route, Routes, useNavigate } from 'react-router-dom';
import Home from './Components/Home/Home';
import Navbar from './Components/Navbar/Navbar';
import About from './Components/About/About';
import Tv from './Components/Tv/Tv';
import Movies from './Components/Movies/Movies';
import Login from './Components/Login/Login';
import Register from './Components/Register/Register';
import Notfund from './Components/Notfund/Notfund';
import React, { useEffect, useState } from 'react'
import jwtDecode from 'jwt-decode';
import MovieDetails from './Components/MovieDetails/MovieDetails';
import TvDetails from './Components/TvDetails/TvDetails';
import Season from './Components/Season/Season';

function App() {
  const [userData, setUserData] = useState(null)
  let navigate = useNavigate()


  useEffect(() => {
    if (localStorage.getItem('userToken')) {
      gitUserData()
    }
  }, [])

  function gitUserData() {
    let decodetToken = jwtDecode(localStorage.getItem('userToken'));
    setUserData(decodetToken)
  }

  function logOut() {
    localStorage.removeItem('userToken')
    setUserData(null)
    navigate('/login')
  }

  function ProtectedRoute({ children }) {
    if (!localStorage.getItem('userToken')) {
      return <Navigate to='/login' />
    }
    else {
      return children
    }
  }


  return (
    <div>
      <Navbar userData={userData} logOut={logOut} />
      <Routes>
        <Route path='/' element={<ProtectedRoute><Home /></ProtectedRoute>} />
        <Route path='noxe' element={<ProtectedRoute><Home /></ProtectedRoute>} />
        <Route path='noxe/about' element={<ProtectedRoute><About /></ProtectedRoute>} />
        <Route path='noxe/tv' element={<ProtectedRoute><Tv /></ProtectedRoute>} />
        <Route path='noxe/movies' element={<ProtectedRoute><Movies /></ProtectedRoute>} />
        <Route path='noxe/moviedetails' element={<ProtectedRoute><MovieDetails /></ProtectedRoute>}>
          <Route path=':id' element={<MovieDetails />} />
        </Route>
        <Route path='noxe/tvdetails' element={<ProtectedRoute><TvDetails /></ProtectedRoute>}>
          <Route path=':id' element={<TvDetails />} />
        </Route>
        <Route path='noxe' element={<ProtectedRoute><Season /></ProtectedRoute>} >
          <Route path=':id' element={<Season />} >
            <Route path=':tvname/season' element={<Season />} >
              <Route path=':idSeason' element={<Season />} />
            </Route>
          </Route>
        </Route>
        <Route path='login' element={<Login gitUserData={gitUserData} />} />
        <Route path='register' element={<Register />} />
        <Route path='*' element={<Notfund />} />
      </Routes>
    </div>
  );
}

export default App;
