import axios from 'axios'
import React, { useEffect, useState } from 'react'
import './Home.css'
import { Link } from 'react-router-dom';
import img1 from '../images/1.jpg'
import img2 from '../images/2.jpg'

export default function Home() {

  const [trendingMovies, setTrendingMovies] = useState([]);
  const [trendingTv, setTrendingTv] = useState([]);
  const [trendingPersons, setTrendingPersons] = useState([]);
  const [upcomingMovie, setUpcomingMovie] = useState([]);
  const baseImgeUrl = 'https://image.tmdb.org/t/p/original/';

  async function getTrendingItems(medaTyep, callback) {
    let { data } = await axios.get(`https://api.themoviedb.org/3/trending/${medaTyep}/day?api_key=c636ed7787cc302d96bf88ccf334e0d8`);
    callback(data.results)
  }
  async function getUpcomingMovie() {
    let { data } = await axios.get(`https://api.themoviedb.org/3/movie/upcoming?api_key=c636ed7787cc302d96bf88ccf334e0d8`);
    setUpcomingMovie(data.results)
  }
  useEffect(() => {
    getTrendingItems('movie', setTrendingMovies)
    getTrendingItems('tv', setTrendingTv)
    getTrendingItems('person', setTrendingPersons)
    getUpcomingMovie()
  }, [])

  return (<>
    <div id="carouselExampleSlidesOnly" class="carousel slide" data-bs-ride="carousel">
      <div style={{marginRight: '5%' , marginLeft: '5%'}} class="carousel-indicators">
        <div className="">
          <h3 className='mb-3' style={{ fontWeight: '600' , }}>Movie Coming Soon</h3>
          <div className="row gx-1">
            {upcomingMovie.map((upcoming, index) => index < 10 ?
              <div className="col-lg-1" key={index}>
                <img className='w-100' src={baseImgeUrl + upcoming.poster_path} alt="" />
              </div>
              : '')}
          </div>
        </div>
      </div>
      <div class="carousel-inner">
        <div class="carousel-item active">
          <img src={img1} class="d-block w-100" alt="..." />
        </div>
        <div class="carousel-item">
          <img src={img2} class="d-block w-100" alt="..." />
        </div>
      </div>
    </div>
    {/* <div className='backgroun_home' >
      <div className="container">
        <h3 style={{ fontWeight: '600' }}>Movie Upcoming Soon</h3>
        <div className="row gx-1">
          {upcomingMovie.map((upcoming, index) => index < 9 ?
            <div className="col-lg-1" key={index}>
              <img className='w-100' src={baseImgeUrl + upcoming.poster_path} alt="" />
            </div>
            : '')}
        </div>
      </div>
    </div> */}
    <div className='container'>
      <div className="row mt-4">
        <div className='col-md-4 d-flex align-items-center'>
          <div className='w-100'>
            <div className='bordr w-50 my-3'></div>
            <h4>Trending <br />Movies <br />to Wathch Now </h4>
            <p className='secondColor'>most watched movies by days</p>
            <div className='bordr my-3'></div>
          </div>
        </div>
        {trendingMovies.map((movie, index) => index < 10 ? <div className='col-md-2 my-2' key={index}>
          <div className='itemHover'>
            <Link to={`/noxe/moviedetails/${movie.id}`}>
              <img className='w-100' src={baseImgeUrl + movie.poster_path} alt="" />
              <span className='vote_home'>{movie.vote_average.toFixed(1)}</span>
              <h6 style={{ fontWeight: '600' }}>{movie.title}</h6>
            </Link>
          </div>
        </div> : '')}
        <Link to='/noxe/movies' className="col-md-12 text-center button_detalis">
          <div>
            <i className="fa-solid fa-chevron-down fs-2"></i>
          </div>
        </Link>
      </div>

      <div className="row my-4">
        <div className='col-md-4 d-flex align-items-center'>
          <div className='w-100'>
            <div className='bordr w-50 my-3'></div>
            <h4>Trending <br />Movies <br />to Wathch Now </h4>
            <p className='secondColor'>most watched movies by days</p>
            <div className='bordr my-3'></div>
          </div>
        </div>
        {trendingTv.map((tv, index) => index < 10 ? <div className='col-md-2 my-3' key={index}>
          <div className='itemHover'>
            <Link to={`/noxe/tvdetails/${tv.id}`}>
              <img className='w-100' src={baseImgeUrl + tv.poster_path} alt="" />
              <span className='vote_home'>{tv.vote_average.toFixed(1)}</span>
              <h6 style={{ fontWeight: '600' }}>{tv.name}</h6>
            </Link>
          </div>
        </div> : '')}
        <Link to='/noxe/tv' className="col-md-12 text-center button_detalis">
          <div>
            <i className="fa-solid fa-chevron-down fs-2"></i>
          </div>
        </Link>
      </div>

      <div className="row">
        <div className='col-md-4 d-flex align-items-center'>
          <div className='w-100'>
            <div className='bordr w-50 my-3'></div>
            <h4>Trending <br />Person <br />to Wathch Now </h4>
            <p className='secondColor'>most watched Person by days</p>
            <div className='bordr my-3'></div>
          </div>
        </div>
        {trendingPersons.map((person, index) => person.profile_path !== null & index < 10 ? <div className='col-md-2 my-3' key={index}>
          <div>
            <Link to={`/tvdetails/${person.id}`}>
              <img className='w-100' src={baseImgeUrl + person.profile_path} alt="" />
              <h6 style={{ fontWeight: '600' }}>{person.name}</h6>
            </Link>
          </div>
        </div> : '')}
        <div className="col-md-12 text-center button_detalis">
          <div>
            <i className="fa-solid fa-chevron-down fs-2"></i>
          </div>
        </div>
      </div>
    </div>
  </>
  )
}
