import React from 'react'
import { Link } from 'react-router-dom'


export default function Navbar(props) {
  return (<>
    <nav className="navbar navbar-expand-lg navbar-dark ">
      <div className="container-fluid">
        <Link to="noxe" className="navbar-brand">NOXE</Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id='navbarSupportedContent'>
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            {
              props.userData ? <>
                <li className="nav-item">
                  <Link className='nav-link' to="noxe">Home</Link>
                </li>
                <li className="nav-item">
                  <Link className='nav-link' to="noxe/about">About</Link>
                </li>
                <li className="nav-item">
                  <Link className='nav-link' to="noxe/tv">Tv</Link>
                </li>
                <li className="nav-item">
                  <Link className='nav-link' to="noxe/movies">Movies</Link>
                </li>
              </> : ''
            }

          </ul>
          <ul className="navbar-nav ms-auto mb-2 mb-lg-0 ">
            <li className="nav-item d-flex align-items-center me-4">
              <i className="fab mx-2 fa-instagram"></i>
              <i className="fab mx-2 fa-facebook"></i>
              <i className="fab mx-2 fa-youtube"></i>
              <i className="fab mx-2 fa-spotify"></i>
            </li>
            {
              props.userData ? <>
                <li className="nav-item ">
                  <span className='nav-link btn_Nav' onClick={props.logOut} >Logout</span>
                </li>
              </> : <>
                <li className="nav-item">
                  <Link className='nav-link btn_Nav' to="register">Register</Link>
                </li>
                <li className="nav-item">
                  <Link className='nav-link btn_Nav' to="login">Login</Link>
                </li>
              </>
            }


          </ul>
        </div>
      </div>
    </nav>
  </>
  )
}
