import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'



function Season() {

  const [seasonDetails, setSeasonDetails] = useState([]);
  const [episodesDetails, setEpisodesDetails] = useState([]);
  let params = useParams()
  const baseImgeUrl = 'https://image.tmdb.org/t/p/original/'


  async function getSeasonDetails() {
    let { data } = await axios.get(`https://api.themoviedb.org/3/tv/${params.id}/season/${params.idSeason}?api_key=c636ed7787cc302d96bf88ccf334e0d8`);
    setSeasonDetails(data)
    setEpisodesDetails(data.episodes)
    console.log(seasonDetails);
  }

  useEffect(() => {
    getSeasonDetails()
  }, [])

  return (<>
    <div className='container_details mt-5'>
      <div className="row">
        <div className="col-lg-3">
          <img className='w-100' src={baseImgeUrl + seasonDetails.poster_path} alt="" />
        </div>
        <div className="col-lg-8">
          <div className="row mt-5">
            <div className="col-lg-12">
              <h3 className='title_details'>{params.tvname}</h3>
              <h3 className='title_details'>{seasonDetails.name}</h3>
              <p className='mt-4 '>{seasonDetails.overview}</p>
            </div>
          </div>
        </div>
      </div>
      <div className="row mt-5">
        {episodesDetails.map((episode, index) =>
          <div className="col-lg-2" key={index}>
            <img className='w-100' src={baseImgeUrl + episode.still_path} alt="" />
            <p className='text-center' style={{ fontWeight: '600' }}>({episode.episode_number})</p>
            <span className='vote_episode'>{episode.vote_average.toFixed(1)}</span>
          </div>
        )}
      </div>
    </div>
  </>
  )
}

export default Season