import axios from 'axios';
import Joi from 'joi';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';


export default function Register() {

  let navigate = useNavigate();
  const [errorList, setErrorList] = useState('')
  const [isLoding, setIsLoding] = useState(false)
  const [error, setError] = useState('')
  const [user, setUser] = useState({
    first_name: '',
    last_name: '',
    email: '',
    password: '',
    age: 0
  });

  function getUser(e) {
    let myUser = { ...user };
    myUser[e.target.name] = e.target.value
    setUser(myUser)
  }

  async function submitRegister(e) {
    e.preventDefault();
    setIsLoding(true)
    let validationResult = validateRegidterForm(user);

    if (validationResult.error) {
      // list error
      setIsLoding(false)
      setErrorList(validationResult.error.details)
      console.log(validationResult.error.details)
    }

    else {
      let { data } = await axios.post('https://route-egypt-api.herokuapp.com/signup', user);
      if (data.message === 'success') {
        // login page
        setIsLoding(false)
        navigate('/login')
      }
      else {
        // error
        setError(data.message)
        setIsLoding(false)
      }
    }

  }

  function validateRegidterForm() {
    let schema = Joi.object({
      first_name: Joi.string().alphanum().min(3).max(8).required(),
      last_name: Joi.string().alphanum().min(3).max(8).required(),
      age: Joi.number().min(16).max(80).required(),
      email: Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net', 'yahoo'] } }),
      password: Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$')),
    });
    return schema.validate(user, { abortEarly: false })
  }

  return (
    <div className='container'>
      <h2 className='my-3'>Register Now</h2>
      {/* {errorList ? errorList.map((error, index) => {
        if (index === 4) {
          return <div key={index} className='alert-error'>The Password is incorrect</div>
        }
        else {
          return <div key={index} className='alert-error'>{error.message}</div>
        }
      }) : ''} */}

      <form className='py-4' onSubmit={submitRegister}>
        <label htmlFor="first_name">first name :</label>
        <input onChange={getUser} type="text" className='form-control my-3' name='first_name' id='first_name' />
        {errorList ? errorList.map((error) => error.context.key === 'first_name' ? <div className='alert-error'>The first name is incorrect</div> : '') : ''}
        <label label htmlFor="last_name">last name :</label>
        <input onChange={getUser} type="text" className='form-control my-3' name='last_name' id='last_name' />
        {errorList ? errorList.map((error) => error.context.key === 'last_name' ? <div className='alert-error'>The last name is incorrect</div> : '') : ''}
        <label htmlFor="email">Email :</label>
        <input onChange={getUser} type="email" className='form-control my-3' name='email' id='email' />
        {errorList ? errorList.map((error) => error.context.key === 'email' ? <div className='alert-error'>The Email is incorrect</div> : '') : ''}
        {error ? <div className="alert-error">{error.substring(error.lastIndexOf(':') + 2)}</div> : ''}
        <label htmlFor="age">Age :</label>
        <input onChange={getUser} type="number" className='form-control my-3' name='age' id='age' />
        {errorList ? errorList.map((error) => error.context.key === 'age' ? <div className='alert-error'>The age is incorrect</div> : '') : ''}
        <label htmlFor="password">Password :</label>
        <input onChange={getUser} type="password" className='form-control my-3' name='password' id='password' />
        {errorList ? errorList.map((error) => error.context.key === 'password' ? <div className='alert-error'>The Password is incorrect</div> : '') : ''}
        <button className='btn btn-outline-info'>{isLoding ? <i className='fas fa-spinner fa-spin'></i> : "register"}</button>
      </form>
    </div >
  )
}
